import type {
  NoraAPIGetCountriesResponse,
  NoraAPIGetLanguagesRequest,
  NoraAPIGetLanguagesResponse,
  NoraAPIGetCurrenciesRequest,
  NoraAPIGetCurrenciesResponse,
  NoraAPIGetCountriesRequest,
} from './types/dictionaries.types';
import type { AxiosRequestConfig } from 'axios';
import type { ContextAPI } from './types';

export class DictionariesAPI {
  readonly path = '/dictionaries';
  readonly context: ContextAPI;

  constructor(ctx: ContextAPI) {
    this.context = ctx;
  }

  /**
   * Get avaiable countries
   * @param data SubscriptionsAPIGetRequest
   * @param config AxiosConfig
   * @returns List of available subscriptions
   */
  getCountries(data?: NoraAPIGetCountriesRequest, config?: AxiosRequestConfig): Promise<NoraAPIGetCountriesResponse> {
    return this.context.post<NoraAPIGetCountriesResponse, NoraAPIGetCountriesRequest>(
      `${this.path}/countries/get`,
      data || {},
      config
    );
  }

  /**
   * Get avaiable languages
   * @param data SubscriptionsAPIGetRequest
   * @param config AxiosConfig
   * @returns List of available subscriptions
   */
  getLanguages(data?: NoraAPIGetLanguagesRequest, config?: AxiosRequestConfig): Promise<NoraAPIGetLanguagesResponse> {
    return this.context.post<NoraAPIGetLanguagesResponse, NoraAPIGetLanguagesRequest>(
      `${this.path}/languages/get`,
      data || {},
      config
    );
  }

  /**
   * Get avaiable currencies
   * @param data SubscriptionsAPIGetRequest
   * @param config AxiosConfig
   * @returns List of available subscriptions
   */
  getCurrencies(
    data?: NoraAPIGetCurrenciesRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraAPIGetCurrenciesResponse> {
    return this.context.post<NoraAPIGetCurrenciesResponse, NoraAPIGetCurrenciesRequest>(
      `${this.path}/currencies/get`,
      data || {},
      config
    );
  }
}
