import type {
  PaymentsAPIDoRequest,
  PaymentsAPIDoResponse,
  PaymentsAPIDoSingleRequest,
  PaymentsAPIDoSingleResponse,
  PaymentsAPICalculateRequest,
  PaymentsAPICalculateResponse,
  PaymentsAPICalculateContentSetsRequest,
  PaymentsAPICalculateContentSetsResponse,
  PaymentsAPIPayContentSetsRequest,
  PaymentsAPIPayContentSetsResponse,
} from './types/payments.types';
import type { AxiosRequestConfig } from 'axios';
import type { ContextAPI } from './types';

export class PaymentsAPI {
  readonly path = '/payments';
  readonly context: ContextAPI;

  constructor(ctx: ContextAPI) {
    this.context = ctx;
  }
  /**
   * Creating payment for subscriber
   * @param data Payment data
   * @param config AxiosConfiguration
   */
  payReSubscription(data: PaymentsAPIDoRequest, config?: AxiosRequestConfig): Promise<PaymentsAPIDoResponse> {
    return this.context.post<PaymentsAPIDoResponse, PaymentsAPIDoRequest>(`${this.path}/do`, data, config);
  }

  /**
   * Creating subscriber, credit card and make payment for subscriber
   * @param data Full data about subscriber, credit card and payment
   * @param config AxiosConfiguration
   */
  paySubscription(data: PaymentsAPIDoSingleRequest, config?: AxiosRequestConfig): Promise<PaymentsAPIDoSingleResponse> {
    return this.context.post<PaymentsAPIDoSingleResponse, PaymentsAPIDoSingleRequest>(
      `${this.path}/doSingle`,
      data,
      config
    );
  }

  /**
   * Calculates amount of subscriber's payment
   * @param data Required info for calculation
   * @param config AxiosConfiguration
   * @returns Calculation for needed subscription
   */
  calculate(data: PaymentsAPICalculateRequest, config?: AxiosRequestConfig): Promise<PaymentsAPICalculateResponse> {
    return this.context.post<PaymentsAPICalculateResponse, PaymentsAPICalculateRequest>(
      `${this.path}/calculate`,
      data,
      config
    );
  }

  /**
   * Calculate Content Set Addon Prices
   * @param data Required info for calculation
   * @param config AxiosConfiguration
   * @returns Calculation for needed subscription
   */
  calculateContentSets(
    data: PaymentsAPICalculateContentSetsRequest,
    config?: AxiosRequestConfig
  ): Promise<PaymentsAPICalculateContentSetsResponse> {
    return this.context.post<PaymentsAPICalculateContentSetsResponse, PaymentsAPICalculateContentSetsRequest>(
      `${this.path}/content-sets/calculate`,
      data,
      config
    );
  }

  /**
   * Pay Content Set Addons
   * @param data Required info for calculation
   * @param config AxiosConfiguration
   * @returns Calculation for needed subscription
   */
  payContentSets(
    data: PaymentsAPIPayContentSetsRequest,
    config?: AxiosRequestConfig
  ): Promise<PaymentsAPIPayContentSetsResponse> {
    return this.context.post<PaymentsAPIPayContentSetsResponse, PaymentsAPIPayContentSetsRequest>(
      `${this.path}/content-sets/do`,
      data,
      config
    );
  }
}
