import type { AxiosRequestConfig } from 'axios';
import type { ContextAPI } from './types';
import type {
  NoraApiCreateCreditCardsRequest,
  NoraApiCreateCreditCardsResponse,
  NoraApiDeleteCreditCardRequest,
  NoraApiSetDefaultCreditCardRequest,
  NoraApiSetDefaultCreditCardResponse,
  NoraApiGetPaymentMethodResponse,
  NoraApiGetPaymentMethodReuqest,
  NoraApiSetDefaultWalletRequest,
  NoraApiSetDefaultWalletResponse,
  NoraApiDeleteWalletRequest,
  NoraApiDeleteWalletResponse,
  NoraApiDeleteCreditCardResponse,
  NoraApiAddPaymentMethodRequest,
  NoraApiAddPaymentMethodResponse,
} from './types/payment-method.types';

export class PaymentMethodsAPI {
  readonly path = '/payment-method';
  readonly context: ContextAPI;

  constructor(ctx: ContextAPI) {
    this.context = ctx;
  }

  /**
   * Get saved tokenized payment methods for subscriber
   * @param data
   * @param config
   */
  get(data: NoraApiGetPaymentMethodReuqest, config?: AxiosRequestConfig): Promise<NoraApiGetPaymentMethodResponse> {
    return this.context.post<NoraApiGetPaymentMethodResponse, NoraApiGetPaymentMethodReuqest>(
      `${this.path}/get`,
      data,
      config
    );
  }

  /**
   * Add tokenized payment method
   * @param data tokenized wallet data
   * @param config axios config
   * @returns
   */
  add(data: NoraApiAddPaymentMethodRequest, config?: AxiosRequestConfig): Promise<NoraApiAddPaymentMethodResponse> {
    return this.context.post<NoraApiAddPaymentMethodResponse, NoraApiAddPaymentMethodRequest>(
      `${this.path}/add`,
      data,
      config
    );
  }

  /**
   * Set tokenized wallet as default payment method for auto payments
   * @param data auth data
   * @param walletId walletId
   * @param config axios config
   * @returns
   */
  setDefaultWallet(
    data: NoraApiSetDefaultWalletRequest,
    walletId: number,
    config?: AxiosRequestConfig
  ): Promise<NoraApiSetDefaultWalletResponse> {
    return this.context.post<NoraApiSetDefaultWalletResponse, NoraApiSetDefaultWalletRequest>(
      `${this.path}/wallet/${walletId}/default`,
      data,
      config
    );
  }

  /**
   * Delete subscriber’s stored wallet
   * @param data auth data
   * @param creditCardId wallet Id
   * @param config AxiosConfiguration
   */
  deleteWallet(
    data: NoraApiDeleteWalletRequest,
    walletId: number,
    config?: AxiosRequestConfig
  ): Promise<NoraApiDeleteWalletResponse> {
    return this.context.post<NoraApiDeleteWalletResponse, NoraApiDeleteWalletRequest>(
      `${this.path}/wallet/${walletId}/delete`,
      data,
      config
    );
  }

  /**
   * This call creates new credit card for Subscriber with given accountNumber and lastName.
   * @param data Credit card info
   * @param config AxiosConfiguration
   * @returns Created credit card Id
   */
  createCreditCard(
    data: NoraApiCreateCreditCardsRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraApiCreateCreditCardsResponse> {
    return this.context.post<NoraApiCreateCreditCardsResponse, NoraApiCreateCreditCardsRequest>(
      `/creditcards/create`,
      data,
      config
    );
  }

  /**
   * Set credit card as default payment method for auto payments
   * @param data Auth info
   * @param creditCardId creditCardId
   * @param config AxiosConfiguration
   */
  setDefaultCreditCard(
    data: NoraApiSetDefaultCreditCardRequest,
    creditCardId: number,
    config?: AxiosRequestConfig
  ): Promise<NoraApiSetDefaultCreditCardResponse> {
    return this.context.post<NoraApiSetDefaultCreditCardResponse, NoraApiSetDefaultCreditCardRequest>(
      `${this.path}/creditcard/${creditCardId}/default`,
      data,
      config
    );
  }

  /**
   * Delete subscriber’s stored credit card
   * @param data auth data
   * @param creditCardId credit card Id
   * @param config AxiosConfiguration
   */
  deleteCreditCard(
    data: NoraApiDeleteCreditCardRequest,
    creditCardId: number,
    config?: AxiosRequestConfig
  ): Promise<NoraApiDeleteCreditCardResponse> {
    return this.context.post<NoraApiDeleteCreditCardResponse, NoraApiDeleteCreditCardRequest>(
      `${this.path}/creditcard/${creditCardId}/delete`,
      data,
      config
    );
  }
}
