import { SubscriptionsAPI } from './subscriptions';
import { PaymentsAPI } from './payments';
import * as configuration from '../configure';
import { SubscribersAPI } from './subscribers';
import { client } from './../client';

import type { APIConfig, ContextAPI } from './types';
import type { AxiosInstance, AxiosResponse } from 'axios';
import { NoraApiError } from '../errors/nora-api.error';
import { PaymentSystemsAPI } from './payment-systems';
import { PaymentMethodsAPI } from './payment-methods';
import { DictionariesAPI } from './dictionaries';

const {
  defaultOptions: { token, login, baseURL },
} = configuration;

export class NoraAPI implements ContextAPI {
  readonly config: APIConfig;
  readonly logger: unknown;

  // resources
  readonly subscribers: SubscribersAPI;
  readonly payments: PaymentsAPI;
  readonly subscriptions: SubscriptionsAPI;
  readonly paymentSystems: PaymentSystemsAPI;
  readonly paymentMethods: PaymentMethodsAPI;
  readonly dictionaries: DictionariesAPI;
  constructor(cfg?: APIConfig) {
    // resolve config
    this.config = {
      baseURL: cfg?.baseURL || baseURL,
      token: cfg?.token || token,
      login: cfg?.login || login,
    };

    this.subscribers = new SubscribersAPI(this);
    this.payments = new PaymentsAPI(this);
    this.subscriptions = new SubscriptionsAPI(this);
    this.paymentSystems = new PaymentSystemsAPI(this);
    this.paymentMethods = new PaymentMethodsAPI(this);
    this.dictionaries = new DictionariesAPI(this);
  }

  send(): AxiosInstance {
    // TODO: may be support multiple clients
    const { token, login } = this.config;
    client.interceptors.request.use(function (cfg) {
      // Mixin token to auth data
      if (Object.prototype.hasOwnProperty.call(cfg, 'data')) {
        const { data } = cfg;
        cfg.data = {
          ...data,
          auth: { token, login, ...data.auth },
        };
      } else {
        cfg.data = { auth: { token, login } };
      }
      return cfg;
    });

    return client;
  }

  async post<R = any, D = any>(url: string, data?: D, config?: any): Promise<R> {
    try {
      const result = await this.send().post<unknown, AxiosResponse<R>, D>(`${this.config.baseURL}${url}`, data, config);
      return result?.data;
    } catch (error: any) {
      // TODO: format errors remove AxiosError
      throw new NoraApiError(error);
    }
  }

  async get<R = any, D = any>(url: string, config?: any): Promise<R> {
    try {
      const result = await this.send().get<unknown, AxiosResponse<R>, D>(`${this.config.baseURL}${url}`, config);
      return result?.data;
    } catch (error: any) {
      // TODO: format errors remove AxiosError
      throw new NoraApiError(error);
    }
  }
}
