import type {
  NoraAPIGetSubscriberByEmailRequest,
  NoraAPIGetSubscriberByEmailResponse,
  NoraAPIGetSubscriberInfoRequest,
  NoraAPIGetSubscriberRequest,
  NoraAPIGetSubscriberResponse,
  NoraApiSubscriberAutoPaySubscriptionRequest,
  NoraApiSubscriberAutoPaySubscriptionResponse,
  NoraApiSubscriberCancelSubscriptionRequest,
  NoraApiSubscriberCancelSubscriptionResponse,
  NoraApiSubscriberSubscriptionContentSetsRequest,
  NoraApiSubscriberSubscriptionContentSetsResponse,
  NoraApiSubscriberSubscriptionGetRequest,
  NoraApiSubscriberSubscriptionGetResponse,
  NoraApiSusbscriberUnassignDeviceRequest,
  NoraApiSusbscriberUnassignDeviceResponse,
  NoraAPIUpdateSubscriberInfoRequest,
  NoraAPIUpdateSubscriberResponse,
} from './types/subscribers.types';
import type { AxiosRequestConfig } from 'axios';
import type { ContextAPI } from './types';

export class SubscribersAPI {
  readonly path = '/subscribers';
  readonly context: ContextAPI;

  constructor(ctx: ContextAPI) {
    this.context = ctx;
  }

  /**
   * Get subscriber by accountNumber and last name
   * @param data Subscriber auth info
   * @param config AxiosConfig
   * @returns Subscriber detail info
   */
  get(data: NoraAPIGetSubscriberRequest, config?: AxiosRequestConfig): Promise<NoraAPIGetSubscriberResponse> {
    return this.context.post<NoraAPIGetSubscriberResponse, NoraAPIGetSubscriberRequest>(
      `${this.path}/get`,
      data,
      config
    );
  }

  /**
   * Get subscribers by email
   * @param data Subscriber email
   * @param config AxiosConfig
   * @returns Subscribers detail info
   */
  getByEmail(
    data: NoraAPIGetSubscriberByEmailRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraAPIGetSubscriberByEmailResponse> {
    return this.context.post<NoraAPIGetSubscriberByEmailResponse, NoraAPIGetSubscriberByEmailRequest>(
      `${this.path}/search`,
      data,
      config
    );
  }

  /**
   * Get subscriber by login and password
   * @param data Subscriber data info
   * @param config AxiosConfig
   * @returns Subscriber detail info
   */
  info(data: NoraAPIGetSubscriberInfoRequest, config?: AxiosRequestConfig): Promise<NoraAPIGetSubscriberResponse> {
    return this.context.post<NoraAPIGetSubscriberResponse, NoraAPIGetSubscriberInfoRequest>(
      `${this.path}/info`,
      data,
      config
    );
  }

  /**
   * Update Subscriber
   * @description This call will update subscriber
   * @param data Subscriber data info
   * @param config AxiosConfig
   * @returns Subscriber detail info
   */
  update(
    data: NoraAPIUpdateSubscriberInfoRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraAPIUpdateSubscriberResponse> {
    return this.context.post<NoraAPIUpdateSubscriberResponse, NoraAPIUpdateSubscriberInfoRequest>(
      `${this.path}/update`,
      data,
      config
    );
  }

  /**
   * Loading subscriber subscription content sets
   * @param config AxiosConfig
   * @returns Subscription content sets info
   */
  getSubscriptionContentSets(
    data: NoraApiSubscriberSubscriptionContentSetsRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraApiSubscriberSubscriptionContentSetsResponse> {
    return this.context.post<
      NoraApiSubscriberSubscriptionContentSetsResponse,
      NoraApiSubscriberSubscriptionContentSetsRequest
    >(`${this.path}/subscription/content-sets/get`, data, config);
  }

  /**
   * Loading subscriber subscription info
   * @param config AxiosConfig
   * @returns Subscription content sets info
   */
  getSubscription(
    data: NoraApiSubscriberSubscriptionGetRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraApiSubscriberSubscriptionGetResponse> {
    return this.context.post<NoraApiSubscriberSubscriptionGetResponse, NoraApiSubscriberSubscriptionGetRequest>(
      `${this.path}/subscription/get`,
      data,
      config
    );
  }

  /**
   * Unassign device call
   * @param data auth data
   * @param deviceSerialNumber Device serial number
   * @param config axios config
   */
  unassignDevice(
    data: NoraApiSusbscriberUnassignDeviceRequest,
    deviceSerialNumber: string,
    config?: AxiosRequestConfig
  ): Promise<NoraApiSusbscriberUnassignDeviceResponse> {
    return this.context.post<NoraApiSusbscriberUnassignDeviceResponse, NoraApiSusbscriberUnassignDeviceRequest>(
      `${this.path}/devices/${deviceSerialNumber}/unassign`,
      data,
      config
    );
  }

  /**
   * This call cancels the current subscriber’s subscription. Subscriber should have only one active subscription.
   * @param data auth data
   * @param config axios config
   * @returns
   */
  cancelSubscription(
    data: NoraApiSubscriberCancelSubscriptionRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraApiSubscriberCancelSubscriptionResponse> {
    return this.context.post<NoraApiSubscriberCancelSubscriptionResponse, NoraApiSubscriberCancelSubscriptionRequest>(
      `${this.path}/subscription/cancel`,
      data,
      config
    );
  }

  /**
   * This call updates (increase or decrease) current subscriber’s subscription. Subscriber should have only one active subscription.
   * @param data autopay & auth data
   * @param config axios config
   */
  autoPaySubscription(
    data: NoraApiSubscriberAutoPaySubscriptionRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraApiSubscriberAutoPaySubscriptionResponse> {
    return this.context.post<NoraApiSubscriberAutoPaySubscriptionResponse, NoraApiSubscriberAutoPaySubscriptionRequest>(
      `${this.path}/subscription/autopay`,
      data,
      config
    );
  }
}
